import {
  Email,
  ImagePath,
  Name,
  PersonnelId,
  PhoneNumber,
  UserType,
  YearMonthDay,
} from '../value';

import { AuthenticationInformation } from './AuthenticationInformation';

export interface IPersonnel {
  id: PersonnelId;
  name: Name;
  phoneNumber: PhoneNumber;
  email: Email;
  dateOfBirth: YearMonthDay;
  image: ImagePath;
  userType: UserType;
  authenticationInformation: AuthenticationInformation[];
}

export interface IUpdatePersonnel {
  name?: Name;
  phoneNumber?: PhoneNumber;
  email?: Email;
  dateOfBirth?: YearMonthDay;
  image?: ImagePath;
  userType?: UserType;
  authenticationInformation?: AuthenticationInformation[];
}

export class Personnel {
  readonly id: PersonnelId;
  readonly name: Name;
  readonly phoneNumber: PhoneNumber;
  readonly email: Email;
  readonly dateOfBirth: YearMonthDay;
  readonly image: ImagePath;
  readonly userType: UserType;
  readonly authenticationInformation: AuthenticationInformation[];

  constructor(args: IPersonnel) {
    this.id = args.id;
    this.name = args.name;
    this.phoneNumber = args.phoneNumber;
    this.email = args.email;
    this.dateOfBirth = args.dateOfBirth;
    this.image = args.image;
    this.userType = args.userType;
    this.authenticationInformation = args.authenticationInformation;
  }

  update(args: IUpdatePersonnel): UpdatePersonnel {
    return new UpdatePersonnel({ id: this.id, ...args });
  }
}

export class UpdatePersonnel {
  readonly id: PersonnelId;
  readonly name?: Name;
  readonly phoneNumber?: PhoneNumber;
  readonly email?: Email;
  readonly dateOfBirth?: YearMonthDay;
  readonly image?: ImagePath;
  readonly userType?: UserType;
  readonly authenticationInformation?: AuthenticationInformation[];

  constructor(args: IUpdatePersonnel & { id: PersonnelId }) {
    this.id = args.id;
    this.name = args.name;
    this.phoneNumber = args.phoneNumber;
    this.email = args.email;
    this.dateOfBirth = args.dateOfBirth;
    this.image = args.image;
    this.userType = args.userType;
    this.authenticationInformation = args.authenticationInformation;
  }
}
